/* 导航部分, 分为导航菜单和站点列表两个部分 */
.nav-box{
  width: 60%;
  background-color: rgba(255, 255, 255, 0.1);
}

/* 导航菜单部分 */
.menu {
  width: 100%;
  height: 3.2em;
  background-color: rgba(0, 102, 153, 0.8);
  text-align: center;
  font-size: 1.6rem;
  color: white;
  list-style: none;
}

.menu ul li {
  width: 6em;
  display: inline-block;
  line-height: 3.2em;
  cursor: pointer;
  border-right: 1px solid rgba(0,0,0,0.1);
  position: relative;
}

.menu ul li:first-child {
  border-left: 1px solid rgba(0,0,0,0.1);
}

.menu ul li:hover {
  background-color: rgb(0, 102, 204);
}

/* 站点列表 */
.items {
  padding-left: 4em;
  padding-bottom: 1.4em;
  min-height: 19em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.6rem;
}

/* 清除a标签的默认样式 */
.items a {
  color: white;
  text-decoration: none;
  margin-top: 1.4em;
  margin-right: 4em;
}

.items a li{
  width: 12rem;
  height: 3em;
  line-height: 3em;
  text-align: center;
  border-radius: 15%;
  background-color: rgba(0, 153, 204, 0.8);
}

.items a li:hover {
  cursor: pointer;
  background-color: rgb(0, 102, 204);
}
